import React, { Component } from "react";
import PropTypes from 'prop-types';

import merchants from 'data/merchants.json';


class AddressName extends Component {
  render() {
    const address = this.props.address.toLowerCase();

    if (merchants.hasOwnProperty(address)) {
      const img = ((!!merchants[address].img) ?
        <img className="address-name-logo mr-2" src={ './img/merchants/' + merchants[address].img } alt="merchant logo" /> :
        null
      );
      // const logo = require(imgPath);
      return <div className="h4 font-weight-light mt-2 text-white">
        { img }
        <span>{ merchants[address].name }</span>
      </div>
    }
    return null;
  }
}

AddressName.propTypes = {
  address: PropTypes.string,
};

AddressName.defaultProps = {
  address: '0x0000000000000000000000000000000000000000',
};

export default AddressName;
