import React, { Component } from 'react';
import PropTypes from 'prop-types';
import NumericInput from 'react-numeric-input';
// import Modal from 'react-modal';
import { Modal } from 'react-bootstrap';
import qs from 'qs';

import QrcodeImg from 'components/QrcodeImg.js';


class MerchantReceiveToken extends Component {
  state = {
    baseUrl: "https://selfer.selftoken.co/",
    isAmountChecked: true,
    amount: 100, // SELF
    isShowingPayWithDappBrowserModal: false,
    isShowingPayWithAnyWalletModal: false,
    isTesting: false,
  }

  componentDidMount = () => {
    // change baseUrl to window.location.origin if is testing
    try {
      console.log('MerchantReceiveToken this.props.location', this.props.location);
      const queries = qs.parse(this.props.location.search, { ignoreQueryPrefix: true });
      if (!!queries.testing) {
        const baseUrl = window.location.origin + '/#/';
        this.setState({
          isTesting: true,
          baseUrl,
        });
        console.log('is testing. Change baseUrl to ' + baseUrl);
      }
    } catch (error) {
      console.error('MerchantReceiveToken componentDidMount ', error);
    }
  }

  handleInputChange = (event) => {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value
    });
  }

  handleAmountChange = (value) => {
    this.setState({
      amount: value,
    });
  }

  computeUrl = () => {
    const { accounts } = this.props;

    if (accounts.length === 0) {
      return "";
    }

    const { baseUrl, isAmountChecked, amount, isTesting } = this.state;

    return baseUrl + (isTesting ? 'transfer' : 'trans')
    + "?to=" + accounts[0]
    + ( (isAmountChecked && !!amount) ? ("&self=" + amount.toString()) : "" );
  }

  showPayWithDappBrowserModal = () => {
    this.setState({
      isShowingPayWithDappBrowserModal: true,
      isShowingPayWithAnyWalletModal: false,
    });
  }

  showPayWithAnyWalletModal = () => {
    this.setState({
      isShowingPayWithDappBrowserModal: false,
      isShowingPayWithAnyWalletModal: true,
    });
  }

  closeModals = () => {
    this.setState({
      isShowingPayWithDappBrowserModal: false,
      isShowingPayWithAnyWalletModal: false,
    });
  }

  render() {
    const { strings, accounts } = this.props;

    if (accounts.length === 0) {
      return <div>
        <div className="alert alert-danger">{ strings.msg_no_account }</div>
      </div>;
    }

    const { isAmountChecked, amount } = this.state;
    const selfDappUrl = this.computeUrl();

    return (
      <>
        {/* PayWithDappBrowserModal */}
        <Modal
          show={this.state.isShowingPayWithDappBrowserModal}
          onHide={this.closeModals}
          centered={ true }
        >
          <Modal.Header style={{ padding: "0.5rem" }} closeButton>
          </Modal.Header>
          <Modal.Body style={{padding: "0 1rem"}}>
            <div className="text-center">
              <div className="h4 mb-3">{ strings.Pay_with_DApp_Browser }</div>

              <div className="mb-1">
                <div>{ strings.msg_please_scan_with_any_qr_code_scanner_1 }</div>
                <div>{ strings.msg_please_scan_with_any_qr_code_scanner_2 }</div>
              </div>

              <div className="mb-3">
                <QrcodeImg text={ selfDappUrl } width={ 320 } margin={ 2 } />
                <div className="mb-1 text-additional font-condensed word-wrap-break-word text-white">{ selfDappUrl }</div>
              </div>

              {/* <div className="mb-1 text-additional">
                <div>{ strings.msg_supported_dapp_browsers_1 }</div>
                <div>{ strings.msg_supported_dapp_browsers_2 }</div>
              </div> */}
            </div>
            <div className="text-right mt-3 mb-3">
              <span className="clickable" onClick={ this.showPayWithAnyWalletModal }>{ strings.Pay_with_Any_Wallet + " ▶" }</span>
            </div>
          </Modal.Body>
        </Modal>

        {/* PayWithAnyWalletModal */}
        <Modal
          show={this.state.isShowingPayWithAnyWalletModal}
          onHide={this.closeModals}
          centered
        >
          <Modal.Header style={{ padding: "0.5rem" }} closeButton>
          </Modal.Header>
          <Modal.Body style={{ padding: "0 1rem" }}>
            <div className="text-center">
              <div className="h4 mb-3">{ strings.Pay_with_Any_Wallet }</div>

              <div className="mb-1">
                <div>
                  <span>{ strings.msg_please_send_self_1 }</span>
                  <span className="text-white"> { isAmountChecked ? amount : '' } SELF </span>
                  <span>{ strings.msg_please_send_self_2 }</span>
                </div>
              </div>

              <div className="mb-1">
                <QrcodeImg text={ accounts[0] } width={ 320 } margin={ 2 } />
              </div>

              <div className="mb-1 font-condensed word-wrap-break-word text-white">{ accounts[0] }</div>
            </div>
            <div className="text-left mt-3 mb-3">
              <span className="clickable" onClick={ this.showPayWithDappBrowserModal }>{ "◀ " + strings.Pay_with_DApp_Browser }</span>
            </div>
          </Modal.Body>
        </Modal>

        <div className="text-center">
          <div className="mb-3">
            {/* <label>
              { strings.My_Wallet_Address }
            </label>
            <input
              type="text"
              className="address-input text-muted"
              value={ accounts[0] }
              onChange={ () => {} }
            /> */}
          </div>

          <div className="mb-3">
            <input
              type="checkbox"
              name="isAmountChecked"
              checked={ isAmountChecked }
              onChange={ this.handleInputChange }
              className="form-check-input"
            />
            <label htmlFor="amountCheckbox">
              { strings.Receive_Amount } ({ strings.EN_SELF })
            </label>
            { isAmountChecked &&
              <NumericInput
                id="receiveAmountInput"
                type="number"
                pattern="[\d\.]*"
                mobile
                className="numeric-input"
                min={0}
                step={100}
                value={ amount }
                onChange={this.handleAmountChange}
              />
            }
          </div>

          <div className="mb-3">
            <h6>{ strings.Payment_Method }</h6>
            <button className="solid-rectangle-button bg-golden m-2" onClick={ this.showPayWithDappBrowserModal }>{ strings.DApp_Browser }</button>
            <button className="solid-rectangle-button bg-golden m-2" onClick={ this.showPayWithAnyWalletModal }>{ strings.Any_Wallet }</button>
          </div>
        </div>
      </>
    );
  }
}

MerchantReceiveToken.propTypes = {
  accounts: PropTypes.array,
  strings: PropTypes.object,
};

MerchantReceiveToken.defaultProps = {
  accounts: [],
  strings: {},
};

export default MerchantReceiveToken;
